import * as actions from "../actions/actionTypes";
import {
  putCityToLocalStorage,
  getInitStateCityList,
  deleteCityFromLocalStorage
} from "../utils/helpers";

const initCityList = getInitStateCityList();
const initSelectedCity =
  initCityList.size > 0
    ? initCityList.first().id + initCityList.first().name
    : null;

const initialState = {
  cityList: initCityList,
  selectedCity: initSelectedCity,
  isLoading: false,
  error: null
};

export default function weatherReducer(state = initialState, action) {
  switch (action.type) {
    case actions.FETCH_CITY:
      const { city } = action;
      const idCity = city.id + city.name;

      putCityToLocalStorage(city);

      return {
        ...state,
        error: null,
        cityList: state.cityList.set(idCity, city),
        selectedCity: idCity
      };

    case actions.SET_LOADING:
      return {
        ...state,
        isLoading: action.isLoading
      };

    case actions.SET_ERROR:
      return {
        ...state,
        error: action.error
      };

    case actions.SET_ACTIVE_CITY:
      return {
        ...state,
        error: null,
        selectedCity: action.id + action.name
      };

    case actions.DELETE_CITY:
      const { id } = action;

      deleteCityFromLocalStorage(id);

      let cityList = state.cityList.delete(id);

      let selectedCity = null;

      if (cityList.size !== 0) {
        selectedCity =
          state.selectedCity === id
            ? cityList.first().id + cityList.first().name
            : state.selectedCity;
      }

      return {
        ...state,
        cityList,
        selectedCity
      };

    default:
      return state;
  }
}
