import { OrderedMap } from "immutable";

import { CITY_LIST_STORE_ID } from "../constants/consts";

export const getPosition = new Promise((resolve, reject) => {
  if (navigator.geolocation) {
    navigator.geolocation.getCurrentPosition(
      position => resolve(position.coords),
      error => reject(new Error(error.message))
    );
  } else {
    reject(new Error("Browser does't support geolocation"));
  }
});

export const tryParce = str => {
  try {
    return JSON.parse(str);
  } catch (error) {
    console.error('Error parce data! Method: "tryParce"', error);
    return null;
  }
};

export const putToLocalStorage = (id, value) => {
  localStorage.setItem(id, JSON.stringify(value));
};

export const getFromLocalStorage = id => {
  let data = localStorage.getItem(id);

  return data !== null ? tryParce(data) : null;
};

export const getInitStateCityList = () => {
  const data = getFromLocalStorage(CITY_LIST_STORE_ID);

  return data !== null ? new OrderedMap(data) : new OrderedMap();
};

export const putCityToLocalStorage = city => {
  let data = getFromLocalStorage(CITY_LIST_STORE_ID);
  const id = city.id + city.name;

  data = data === null ? { [id]: city } : { ...data, [id]: city };

  putToLocalStorage(CITY_LIST_STORE_ID, data);
};

export const deleteCityFromLocalStorage = id => {
  const data = getFromLocalStorage(CITY_LIST_STORE_ID);

  if (data !== null) {
    delete data[id];
  }

  putToLocalStorage(CITY_LIST_STORE_ID, data);
};
